import 'common/mobile-content-toggler/MobileContentToggler.css';

import React from 'react';

class Footer extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      showContentButtonVisible: true,
      hideContentButtonVisible: false
    };
  }

  showContentClicked() {
    this.setState({
      showContentButtonVisible: false,
      hideContentButtonVisible: true
    });
    this.props.toggleContent();
  }

  hideContentClicked() {
    this.setState({
      showContentButtonVisible: true,
      hideContentButtonVisible: false
    });
    this.props.toggleContent();
  }

  render() {
    return (
      <div className="row mobile-content-toggler visible-xs-block visible-sm-block">
        <div className="col-xs-12 text-center">
          <button className={'btn btn-default ' + (this.state.showContentButtonVisible ? '': 'hidden')} type="button" onClick={this.showContentClicked.bind(this)}><span className="glyphicon glyphicon-menu-down" aria-hidden="true"></span></button>
          <button className={'btn btn-default ' + (this.state.hideContentButtonVisible ? '': 'hidden')} type="button" onClick={this.hideContentClicked.bind(this)}><span className="glyphicon glyphicon-menu-up" aria-hidden="true"></span></button>
        </div>
      </div>
    );
  }
}

export default Footer;


