import 'services/Services.css';
import servicesEnglishImage from 'img/services-english.png';
import servicesSpanishImage from 'img/services-spanish.png';
import oca from 'img/oca.png';
import ocp from 'img/ocp.png';
import comptiaLinuxImage from 'img/comptia-linux.png';
import awsLogoImage from 'img/aws-logo.png';
import awsTagImage from 'img/aws-tag.png';

import React from 'react';
import { FormattedMessage, injectIntl, intlShape } from 'react-intl';
import MobileContentToggler from 'common/mobile-content-toggler/MobileContentToggler';

class Services extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      contentHiddenForMobile: true
    };
    this.toggleContent = this.toggleContent.bind(this);
  }

  toggleContent() {
    this.setState(function(prevState, props) {
      return {
        contentHiddenForMobile: !prevState.contentHiddenForMobile
      };
    });
  }

  render() {
    var servicesImage = this.props.language === 'es' ? servicesSpanishImage : servicesEnglishImage;

    return (
      <div className="row services">
        <div className="col-xs-12">
          <a id="services" className="anchor">Services</a>

          <div className="row back-end">

            <div className="col-xs-12 col-md-8">

              <div className="mobile-header-decoration visible-xs-block visible-sm-block">
                <div className="background"></div>
                <div className="foreground"></div>
                <h1 className="text"><FormattedMessage id="services.header"/></h1>
              </div>

              <h3><FormattedMessage id="services.backend.header"/></h3>
              <p className="text-justify"><FormattedMessage id="services.backend.introduction"/> <FormattedMessage id="services.backend.languages"/></p>
              <p className="oc-content"><img src={oca} alt="oca" className="oc"/><img src={ocp} alt="ocp" className="oc"/></p>

            </div>

            <div className="col-md-4 visible-md-block visible-lg-block main-decoration text-center">
              <div className="background"></div>
              <div className="foreground"></div>
              <img src={servicesImage} alt="Services"/>
            </div>

            <div className={'col-xs-12 ' + (this.state.contentHiddenForMobile ? 'hidden-for-mobile' : '')}>
              <p className="text-justify"><FormattedMessage id="services.backend.databases"/> <FormattedMessage id="services.backend.scalability"/></p>
              <p><FormattedMessage id="services.backend.listIntroduction"/></p>
              <div className="row">
                <div className="col-xs-12 col-sm-6 col-md-4">
                  <ul>
                    <li>Java 8</li>
                    <li>Git</li>
                    <li>Maven</li>
                    <li>JUnit, Hamcrest, JaCoCo, Mockito, Selenium</li>
                    <li>Spring Framework</li>
                    <li>Tomcat, Apache HTTP</li>
                    <li>MySQL</li>
                    <li>Jenkins, SonarQube</li>
                    <li>AWS (Elastic Beanstalk, VPC, S3, EC2, RDS, DynamoDB, CloudFormation)</li>
                    <li>Linux (Amazon Linux, CentOS, Fedora)</li>
                    <li>Ansible</li>
                  </ul>
                </div>
                <div className="col-xs-12 col-sm-6 col-md-4">
                  <ul>
                    <li>Java 8</li>
                    <li>Git</li>
                    <li>Maven</li>
                    <li>TestNG, Hamcrest, JaCoCo, Mockito, Selenium</li>
                    <li>JEE (JSF, EJB, JPA)</li>
                    <li>Wildfly, Apache HTTP</li>
                    <li>PostgreSQL</li>
                    <li>MongoDB</li>
                  </ul>
                </div>
                <div className="col-xs-12 col-sm-6 col-md-4">
                  <ul>
                    <li>PHP 5.6</li>
                    <li>Git</li>
                    <li>PHPUnit</li>
                    <li>Codeigniter</li>
                    <li>Apache HTTP</li>
                    <li>MySQL</li>
                    <li>Jenkins</li>
                    <li>Vagrant</li>
                  </ul>
                </div>
              </div>
            </div>

          </div>

          <div className={'row devops ' + (this.state.contentHiddenForMobile ? 'hidden-for-mobile' : '')}>
            <div className="col-xs-12">
              <div className="decoration visible-md-block visible-lg-block"></div>
              <h3><FormattedMessage id="services.devops.header"/></h3>
              <p className="text-justify"><FormattedMessage id="services.devops.introduction"/> <FormattedMessage id="services.devops.linuxadmin"/></p>
              <div className="row">
                <div className="col-xs-12 col-md-3 text-center"><p><img src={comptiaLinuxImage} alt="Comptia" className="comptia"/></p></div>
                <div className="col-xs-12 col-md-9"><p className="text-justify"><FormattedMessage id="services.devops.aws"/></p></div>
              </div>
              <div className="row">
                <div className="col-xs-12 col-md-8">
                  <p className="text-justify"><FormattedMessage id="services.devops.vagrantAnsible"/></p>
                  <p className="text-justify"><FormattedMessage id="services.devops.jenkinsSonarQube"/></p>
                </div>
                <div className="col-xs-12 col-md-4 text-center aws-block">
                  <div><img src={awsLogoImage} alt="AWS Logo"/></div>
                  <div><img src={awsTagImage} alt="AWS Tag"/></div>
                </div>
              </div>
            </div>
          </div>

          <div className={'row front-end ' + (this.state.contentHiddenForMobile ? 'hidden-for-mobile' : '')}>
            <div className="col-xs-12">
            <h3><FormattedMessage id="services.frontend.header"/></h3>
            <p className="text-justify"><FormattedMessage id="services.frontend.introduction"/></p>
            <p className="text-justify"><FormattedMessage id="services.frontend.traditional"/></p>
            <p className="text-justify"><FormattedMessage id="services.frontend.angular"/></p>
            <p className="text-justify"><FormattedMessage id="services.frontend.react"/></p>
            </div>
          </div>

          <MobileContentToggler toggleContent={this.toggleContent}/>

        </div>

      </div>
    );
  }
}

Services.propTypes = {
  intl: intlShape.isRequired
};

export default injectIntl(Services);


