import 'bootstrap/dist/css/bootstrap.css';
import 'App.css';

import React from 'react';
import {IntlProvider, addLocaleData} from 'react-intl';
import locale_en from 'react-intl/locale-data/en';
import locale_es from 'react-intl/locale-data/es';
import messages_en from 'translations/en.json';
import messages_es from 'translations/es.json';
import ReactGA from 'react-ga';

import Header from 'common/header/Header';
import Introduction from 'introduction/Introduction';
import Footer from 'common/footer/Footer';
import Experience from './experience/Experience';
import Services from './services/Services';
import Contact from './contact/Contact';

addLocaleData([...locale_en, ...locale_es]);
const messages = {
  'en': messages_en,
  'es': messages_es
}

ReactGA.initialize('UA-107905576-2');
ReactGA.pageview(window.location.pathname + window.location.search);

class App extends React.Component {

  constructor(props) {
    super(props);
    if( localStorage.language ) {
      this.state = { language : localStorage.language };
    }
    else {
      this.state = { language: this.detectBrowserLanguage() };
    }
    this.onLanguageChanged = this.onLanguageChanged.bind(this);
  }

  onLanguageChanged(language) {
    localStorage.setItem('language', language);
    this.setState({
      language: localStorage.language
    });
  }

  detectBrowserLanguage() {
    var language;
    if (navigator.languages !== undefined) {
      language = navigator.languages[0].toLowerCase();
    }
    else if( navigator.language !== undefined ) {
      language = navigator.language.toLowerCase();
    }
    else {
      language = 'en';
    }

    if( language && language.length > 2 ) {
      language = language.slice(0, 2);
    }

    if( !language || ( language !== 'en' && language !== 'es' ) ) {
      language = 'en';
    }

    return language;
  }

  render() {
    return (
      <IntlProvider locale={this.state.language} messages={messages[this.state.language]}>
        <div className="page-wrapper">
          <div className="page-content">
            <Header language={this.state.language} onLanguageChanged={this.onLanguageChanged}/>
            <div className="container main">
              <Introduction/>
              <Experience language={this.state.language}/>
              <Services language={this.state.language}/>
              <Contact language={this.state.language}/>
            </div>
          </div>
          <Footer/>
        </div>
      </IntlProvider>
    );
  }
}

export default App;



