import 'introduction/Introduction.css';
import introduction from 'img/introduction.png'
import React from 'react';
import { FormattedMessage, FormattedHTMLMessage, injectIntl, intlShape } from 'react-intl';

class Introduction extends React.Component {

  render() {
    return (
      <div className="row">
        <div className="col-xs-12">
          <div className="introduction-container">
            <div className="left">
              <h2 className="introduction-header"><FormattedMessage id="introduction.header"/></h2>
              <div className="introduction-body"><FormattedHTMLMessage id="introduction.body"/></div>
            </div>
            <div className="right">
              <div className="background"></div>
              <div className="foreground"></div>
              <div className="img-container text-center">
                <img src={introduction} alt="Introduction" />
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }

}

Introduction.propTypes = {
  intl: intlShape.isRequired
};

export default injectIntl(Introduction);


