import 'experience/Experience.css';
import experienceEnglishImage from 'img/experience-english.png'
import experienceSpanishImage from 'img/experience-spanish.png'
import sonatypeLogo from 'img/sonatype-logo.png'
import tomtomLogo from 'img/tomtom-logo.png'
import beitechLogo from 'img/beitech-logo.png'
import zenwareLogo from 'img/zenware-logo.png'
import avantelLogo from 'img/avantel-logo.png'
import soluciones360Logo from 'img/soluciones-360-logo.png'
import eycLogo from 'img/eyc-logo.png'

import React from 'react';
import { FormattedMessage, FormattedHTMLMessage, injectIntl, intlShape } from 'react-intl';
import ExperienceItem from './ExperienceItem';
import MobileContentToggler from 'common/mobile-content-toggler/MobileContentToggler';

class Experience extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      contentHiddenForMobile: true
    };
    this.toggleContent = this.toggleContent.bind(this);
  }

  toggleContent() {
    this.setState(function(prevState, props) {
      return {
        contentHiddenForMobile: !prevState.contentHiddenForMobile
      };
    });
  }

  render() {
    const formatMessage = this.props.intl.formatMessage;
    var experienceImage = this.props.language === 'es' ? experienceSpanishImage : experienceEnglishImage;

    return (
      <div className="row experience">
        <div className="col-xs-12">
          <a id="experience" className="anchor">Experience</a>

          <div className="row introduction">

            <div className="col-xs-12 visible-xs-block visible-sm-block mobile">
              <div className="background"></div>
              <div className="foreground"></div>
              <h1 className="text"><FormattedMessage id="experience.header"/></h1>
            </div>

            <div className="col-md-5 visible-md-block visible-lg-block desktop">
              <div className="background"></div>
              <div className="foreground"></div>
              <div className="img-container">
                <img src={experienceImage} alt="Experience"/>
              </div>
            </div>

            <div className="col-md-7 text">
              <div className="text-justify"><FormattedHTMLMessage id="experience.introduction"/></div>
              <div className="visible-md-block visible-lg-block logos">
                <div className="first-line">
                  <a href="#sonatype"><img src={sonatypeLogo} alt="Sonatype Logo"/></a>
                </div>
                <div className="second-line">
                  <a href="#tomtom"><img src={tomtomLogo} alt="TomTom Logo"/></a>
                  <a href="#beitech"><img src={beitechLogo} alt="Beitech Logo"/></a>
                  <a href="#zenware"><img src={zenwareLogo} alt="Zenware Logo"/></a>
                </div>
                <div className="third-line">
                  <a href="#avantel"><img src={avantelLogo} alt="Avantel Logo"/></a>
                  <a href="#soluciones360"><img src={soluciones360Logo} alt="Soluciones 360 Logo"/></a>
                  <a href="#envasadorasycentrifugas"><img src={eycLogo} alt="EyC Logo"/></a>
                </div>
              </div>
            </div>
          </div>

          <div className="decoration-a visible-md-block visible-lg-block"></div>
          <div className="decoration-b visible-md-block visible-lg-block"></div>
          <div className="decoration-c visible-md-block visible-lg-block"></div>

          <div className={'row experience-list ' + (this.state.contentHiddenForMobile ? 'hidden-for-mobile' : '')}>
            <div className="col-xs-12">

              <a id="sonatype" className="anchor">Sonatype</a>
              <ExperienceItem header="Sonaype" website="https://www.sonatype.com"
                location={formatMessage({id: 'experience.sonatype.location'})}
                jobTitle={formatMessage({id: 'experience.sonatype.jobTitle'})}
                dateRange={formatMessage({id: 'experience.sonatype.dateRange'})}
                details={[
                  formatMessage({id: 'experience.sonatype.details0'}),
                  formatMessage({id: 'experience.sonatype.details1'})
                ]}/>

              <a id="tomtom" className="anchor">TomTom</a>
              <ExperienceItem header="TomTom" website="https://www.tomtom.com"
                location={formatMessage({id: 'experience.tomtom.location'})}
                jobTitle={formatMessage({id: 'experience.tomtom.jobTitle'})}
                dateRange={formatMessage({id: 'experience.tomtom.dateRange'})}
                details={[
                  formatMessage({id: 'experience.tomtom.details0'}),
                  formatMessage({id: 'experience.tomtom.details1'}),
                  formatMessage({id: 'experience.tomtom.details2'})
                ]}/>

              <a id="beitech" className="anchor">Beitech</a>
              <ExperienceItem header="Beitech" website="http://beitech.co"
                location={formatMessage({id: 'experience.beitech.location'})}
                jobTitle={formatMessage({id: 'experience.beitech.jobTitle'})}
                dateRange={formatMessage({id: 'experience.beitech.dateRange'})}
                details={[
                  formatMessage({id: 'experience.beitech.details0'}),
                  formatMessage({id: 'experience.beitech.details1'}),
                  formatMessage({id: 'experience.beitech.details2'}),
                  formatMessage({id: 'experience.beitech.details3'}),
                  formatMessage({id: 'experience.beitech.details4'})
                ]}/>

              <a id="zenware" className="anchor">Zenware</a>
              <ExperienceItem id="zenware" header="Zenware" website="https://zenware.com.co"
                location={formatMessage({id: 'experience.zenware.location'})}
                jobTitle={formatMessage({id: 'experience.zenware.jobTitle'})}
                dateRange={formatMessage({id: 'experience.zenware.dateRange'})}
                details={[
                  formatMessage({id: 'experience.zenware.details0'}),
                  formatMessage({id: 'experience.zenware.details1'}),
                  formatMessage({id: 'experience.zenware.details2'})
                ]}/>

              <a id="avantel" className="anchor">Avantel</a>
              <ExperienceItem id="avantel" header="Avantel" website="http://avantel.co"
                location={formatMessage({id: 'experience.avantel.location'})}
                jobTitle={formatMessage({id: 'experience.avantel.jobTitle'})}
                dateRange={formatMessage({id: 'experience.avantel.dateRange'})}
                details={[
                  formatMessage({id: 'experience.avantel.details0'})
                ]}/>

              <a id="soluciones360" className="anchor">Soluciones 360</a>
              <ExperienceItem id="soluciones360" header="Soluciones 360" website="http://soluciones360.co"
                location={formatMessage({id: 'experience.soluciones360.location'})}
                jobTitle={formatMessage({id: 'experience.soluciones360.jobTitle'})}
                dateRange={formatMessage({id: 'experience.soluciones360.dateRange'})}
                details={[
                  formatMessage({id: 'experience.soluciones360.details0'})
                ]}/>

              <a id="envasadorasycentrifugas" className="anchor">Envasadoras y Centrifugas</a>
              <ExperienceItem id="envasadorasycentrifugas" header="Envasadoras y Centrifugas"
                website="https://envasadorasycentrifugas.com"
                location={formatMessage({id: 'experience.envasadorasycentrifugas.location'})}
                jobTitle={formatMessage({id: 'experience.envasadorasycentrifugas.jobTitle'})}
                dateRange={formatMessage({id: 'experience.envasadorasycentrifugas.dateRange'})}
                details={[
                  formatMessage({id: 'experience.envasadorasycentrifugas.details0'}),
                  formatMessage({id: 'experience.envasadorasycentrifugas.details1'}),
                  formatMessage({id: 'experience.envasadorasycentrifugas.details2'}),
                  formatMessage({id: 'experience.envasadorasycentrifugas.details3'})
                ]}/>

            </div>
          </div>

          <MobileContentToggler toggleContent={this.toggleContent}/>

        </div>
      </div>
    );
  }

}

Experience.propTypes = {
  intl: intlShape.isRequired
};

export default injectIntl(Experience);


