import React from 'react';
import { FormattedMessage } from 'react-intl';

class ExperienceItem extends React.Component {
  render() {
    return (
      <div id={this.props.id} className="experience-item">
        <h4>{this.props.header}</h4>
        <div><FormattedMessage id="experience.item.website"/>: <a href={this.props.website} target="_blank">{this.props.website}</a></div>
        <div><FormattedMessage id="experience.item.location"/>: {this.props.location}</div>
        <div><FormattedMessage id="experience.item.jobTitle"/>: {this.props.jobTitle}</div>
        <div><FormattedMessage id="experience.item.dateRange"/>: {this.props.dateRange}</div>
        <div><FormattedMessage id="experience.item.details"/>:</div>
        {this.props.details.map( (detail, index) =>
          <div key={index} className="text-justify">- {detail}</div>
        )}
      </div>
    );
  }
}

export default ExperienceItem;


