import 'contact/Contact.css';
import contactSpanishImage from 'img/contact-spanish.png';
import contactEnglishImage from 'img/contact-english.png';

import React from 'react';
import { FormattedMessage, injectIntl, intlShape } from 'react-intl';

class Contact extends React.Component {

  render() {
    const formatMessage = this.props.intl.formatMessage;
    var contactImage = this.props.language === 'es' ? contactSpanishImage : contactEnglishImage;

    return (
      <div className="row contact">
        <a id="contact" className="anchor">Contact</a>

        <div className="col-xs-12 visible-xs-block visible-sm-block mobile-header">
          <div className="background"></div>
          <div className="foreground"></div>
          <h1 className="text"><FormattedMessage id="contact.header"/></h1>
        </div>

        <div className="col-xs-12 col-md-6">
          <p><FormattedMessage id="contact.introduction"/></p>
          <form action="https://formspree.io/anfe0690@gmail.com" method="POST">
            <input type="hidden" name="_subject" value="Web Portfolio" />
            <input type="text" name="_gotcha" />
            <input type="hidden" name="_next" value={window.location.href} />

            <div className="form-group">
              <label htmlFor="contact-name"><FormattedMessage id="contact.name"/></label>
              <input type="text" name="name" className="form-control" id="contact-name"
                  placeholder={formatMessage({id:'contact.name'})} required/>
            </div>

            <div className="form-group">
              <label htmlFor="replyto"><FormattedMessage id="contact.email"/></label>
              <input type="email" name="_replyto" className="form-control" id="replyto"
                  placeholder={formatMessage({id:'contact.email'})} required/>
            </div>

            <div className="form-group">
              <label htmlFor="contact-message"><FormattedMessage id="contact.message"/></label>
              <textarea name="message" id="contact-message" className="form-control"
                  placeholder={formatMessage({id:'contact.message'})} rows="6" required></textarea>
            </div>

            <button type="submit" className="btn btn-primary btn-block">
              <FormattedMessage id="contact.send"/>
            </button>

          </form>
        </div>

        <div className="col-xs-12 col-md-6 visible-md-block visible-lg-block text-center desktop-decoration">
          <div className="background"></div>
          <div className="foreground"></div>
          <img src={contactImage} alt="Contact"/>
        </div>

      </div>
    );
  }
}

Contact.propTypes = {
  intl: intlShape.isRequired
};

export default injectIntl(Contact);


