import 'common/header/Header.css';
import logo from 'logo.png'

import React from 'react';
import { Glyphicon, Button, ListGroup, ListGroupItem } from 'react-bootstrap';
import FlagIcon from 'common/FlagIcon';
import { FormattedMessage, injectIntl, intlShape } from 'react-intl';
import FontAwesomeIcon from '@fortawesome/react-fontawesome';
import faGlobe from '@fortawesome/fontawesome-free-solid/faGlobe';

class Header extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      mobileMenuVisible: false
    };
    this.onLanguageChanged = this.onLanguageChanged.bind(this);
  }

  onLanguageChanged(language) {
    this.props.onLanguageChanged(language);
  }

  onToggleMobileMenuClicked() {
    this.setState(function(prevState, props) {
      return {
        mobileMenuVisible: !prevState.mobileMenuVisible
      };
    });
  }

  render() {
    return (
      <div className="fixed-menu">
        <div className="fixed-menu-content">

          <div className="menu">
            <div className="left">
              <a href="/">
                <img src={logo} alt="Logo" className="logo" />
              </a>
            </div>
            <div className="right">

              { /* Mobile menu toggle */ }
              <Button onClick={this.onToggleMobileMenuClicked.bind(this)} className="hidden-md hidden-lg">
                <Glyphicon glyph="menu-hamburger" />
              </Button>

              { /* Desktop menu */ }
              <div className="desktop hidden-xs hidden-sm">
                <a href="#experience"><FormattedMessage id="header.experience"/></a>
                <a href="#services"><FormattedMessage id="header.services"/></a>
                <a href="#contact"><FormattedMessage id="header.contact"/></a>
                <div className="dropdown">
                  <span><FontAwesomeIcon icon={faGlobe} /> <FormattedMessage id="header.language"/></span>
                  <div className="dropdown-content">
                    <Button onClick={this.onLanguageChanged.bind(this, 'en')}><FlagIcon code="gb"/> English</Button>
                    <Button onClick={this.onLanguageChanged.bind(this, 'es')}><FlagIcon code="es"/> Español</Button>
                  </div>
                </div>
              </div>

            </div>
          </div>

          { /* Mobile menu */ }
          <div className={"mobile-menu hidden-md hidden-lg " + (this.state.mobileMenuVisible ? '' : 'hidden')}>
            <ListGroup>
              <ListGroupItem href="#experience"><FormattedMessage id="header.experience"/></ListGroupItem>
              <ListGroupItem href="#services"><FormattedMessage id="header.services"/></ListGroupItem>
              <ListGroupItem href="#contact"><FormattedMessage id="header.contact"/></ListGroupItem>
              <li className="list-group-item">
                <div className="text-center"><FormattedMessage id="header.language"/></div>
                <div className="flex-container">
                  <Button onClick={this.onLanguageChanged.bind(this, 'en')}><FlagIcon code="gb"/> English</Button>
                  <Button onClick={this.onLanguageChanged.bind(this, 'es')}><FlagIcon code="es"/> Español</Button>
                </div>
              </li>
            </ListGroup>
          </div>

        </div>
      </div>
    );
  }

}

Header.propTypes = {
  intl: intlShape.isRequired
};

export default injectIntl(Header);


